

















































































	export default {
		data() {
			return {
                isDetail: true,
                isChapter: false,
                comic: {},
                isChapterLoad: false,
                isToUp: true,
                chapterData: []
			};
		},
        onLoad(e) {
            uni.showLoading();
            let openid = uni.getStorageSync('openid');
        	let info = JSON.parse(e.detailData);
        	uni.setNavigationBarTitle({
        		title: info.title
        	})
            uni.request({
            	url: this.$requestUrl+'get_comic_info',
            	method: 'GET',
            	data: {
                    comic_id: info.comic_id
                },
            	success: res => {
                    this.comic = res.data.data;
                },
            	fail: () => {},
            	complete: () => {
                    uni.hideLoading();
                }
            });
        },
        methods: {
            showDetail() {
                this.isDetail = true;
                this.isChapter = false;
            },
            showChapter() {
                this.isDetail = false;
                this.isChapter = true;
                if (!this.isChapterLoad) {
                    uni.showLoading();
                	uni.request({
                		url: this.$requestUrl+'get_comic_chapter',
                		method: 'GET',
                		data: {
                            comic_id: this.comic.id
                        },
                		success: res => {
                            this.isChapterLoad = true;
                            this.chapterData = res.data.data;
                        },
                		fail: () => {},
                		complete: () => {
                            uni.hideLoading();
                        }
                	});
                }
            },
            reading() {
                let detail = {
                	comic_id: this.comic.id,
                	title: this.comic.title,
                    chapter: ''
                }
                uni.navigateTo({
                	url: "../comic-detail/comic-detail?detailData=" + JSON.stringify(detail)
                })
            },
            like() {
                // 点赞
                uni.request({
                	url: this.$requestUrl+'like',
                	method: 'GET',
                	data: {
                        comic_id: this.comic.id,
                        openid: openid
                    },
                	success: res => {
                        if (res.data.status == 1) {
                        	uni.showToast({
                                icon: 'success'
                        	});
                        } else {
                            uni.showToast({
                            	title: '点赞失败'
                            });
                        }
                    },
                	fail: () => {},
                	complete: () => {}
                });
            },
            collect() {
                // 收藏
                uni.request({
                	url: this.$requestUrl+'collect',
                	method: 'GET',
                	data: {
                		comic_id: this.comic.id,
                		openid: openid
                	},
                	success: res => {
                		if (res.data.status == 1) {
                			uni.showToast({
                				icon: 'success'
                			});
                		} else {
                			uni.showToast({
                				title: '收藏失败'
                			});
                		}
                	},
                	fail: () => {},
                	complete: () => {}
                });
            },
            goTop() {
                uni.pageScrollTo({
                	scrollTop: 0,
                })
            }
        }
	}



